export const version = 'ICBC v1.3.0'

const httpUrl = () => (window.localStorage.getItem('httpUrl') || '127.0.0.1');
const websocketUrl = () => (window.localStorage.getItem('websocketUrl') || '127.0.0.1');
const httpPort = () => (window.localStorage.getItem('httpPort') || '8000');
const websocketPort = () => (window.localStorage.getItem('websocketPort') || '8000');

const websocketServer = () => `ws://${websocketUrl()}:${websocketPort()}/`;
const httpServer = () => `http://${httpUrl()}:${httpPort()}/`;

function addPrefix(obj, prefix) {
  const obj1 = {};
  for (let key in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, key)) {
      obj1[key] = prefix + obj[key];
    }
  }
  return obj1;
}

const ws = {
  warning: 'warning', //  { 'message': message }
  numQueue: 'numQueue', //  { 'numberOfQueue': 12 }
  mostStaningTime: 'mostStaningTime', //  { 'mostStaningTime': 123 }
  mostContactTime: 'mostContactTime', //  { mostContactTime: 12 }
  offlineImage: 'offlineImage', // video
  crossRegion: 'crossRegion', //  { 'numArea': 3, 'flow': [12, 123] }
  genderRate: 'genderRate', //  { : 20, : 10 }
  ageRate: 'ageRate', //  { 'age': [12, 13, 20, 23] }
  latestDay: 'latestDay', //  { 'population': [12, 34] }
  faceAttr: 'faceAttr', //  [ 'infos': { 'img': base64, 'age': 13, 'gender':  } ]
  abnormal: 'abnormal', //  { 'name': 'knife', 'img': base64 }
  leftover: 'leftover', //  { 'name': 'bag', 'img': base64 }
  managerStatus: 'managerStatus', //  { 'status': '' }
  numRegion: 'numRegion', //  { 'infos': [{ 'name': 'region1', 'numPerson': 12, 'avgStayTime': 123 }] }
}

const http = {
  selectPerson: 'selectPerson', //  { 'x': 0.13, 'y': 0.24 }
  setEntrySize: 'setEntrySize', //  { 'entrySize': 13 }
  setBankCapacity: 'setBankCapacity', //  { 'bankCapacity': 34 }
  setWaitTime: 'setWaitTime', //  { 'waitTime': 12 }
  setWaitNumber: 'setWaitNumber', //  { 'waitNumber': 23 }
  setLeaveTime: 'setLeaveTime', //  { 'leaveTime': 12 }
  setContactTime: 'setContactTime', //  { 'contactTime': 12 }
  getEntrySize: 'getEntrySize', //  { 'entrySize': 13 }
  getBankCapacity: 'getBankCapacity', //  { 'bankCapacity': 34 }
  getWaitTime: 'getWaitTime', //  { 'waitTime': 12 }
  getWaitNumber: 'getWaitNumber', //  { 'waitNumber': 23 }
  getLeaveTime: 'getLeaveTime', //  { 'leaveTime': 12 }
  getContactTime: 'getContactTime', //  { 'contactTime': 12 }
  getLastWeekNum: 'getLastWeekNum', //  { 'lastWeekNum': [3, 3, 7, 9, 2, 3, 6] }
  backgroundShot: 'backgroundShot', // 
}

export const server = () => {
  return {
    ws: addPrefix(ws, websocketServer()),
    http: addPrefix(http, httpServer())
  }
}